.city-item {
    padding: 5px 10px;
    line-height: normal;
    cursor: pointer;
}

.city-item:hover {
    background-color: rgba(99, 168, 199, 0.1);
}

.city-item:not(:last-child) {
    border-bottom: 1px solid #e6e6e6;
}

.city-item span {
    display: block;
}


.city-item__city {
    font-weight: 700;

    color: #333;
}

.city-item__region {
    font-size: 0.7em;
    font-weight: 300;
    color: #999;
}