.selectorChooser {
    position: relative;
}

.selectorChooser__choose-field {
    position: absolute;
    top: 100%;
    width: 100%;
    background-color: #fff;
    z-index: 100;

    border: 1px solid rgb(99, 168, 199);
    border-top: none;
    border-radius: 0 0 4px 4px;
}

.nothing-find {
    display: block;
    color: #333;
    padding: 10px 10px;
    line-height: normal;
}